import React from "react"
import {graphql} from "gatsby"
import Layout from "../Components/Layout";
import {SEO} from "theme-seo";

const Post = ({data, pageContext}) => {
    const post = data.markdownRemark
    return (
        <Layout>
            <SEO title={pageContext.title} description={post.frontmatter.description} keywords={post.frontmatter.keywords}/>
            <main className="text-right w-10/12 lg:w-3/5 mx-auto">
                <div className="WordpressStyles markdownBlog">
                    <div className="lg:py-2 bg-gray-100">
                        <h1 className="lg:p-4 text-gray-600" dangerouslySetInnerHTML={{__html: post.frontmatter.title}}/>
                        <div className="text-gray-600 lg:px-8" dangerouslySetInnerHTML={{__html: post.html}}/>
                    </div>
                </div>
            </main>
        </Layout>
    )
}

export default Post

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      html
      frontmatter {
        title
        description
        keywords
      }
    }
  }
`
